* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  max-width: 1300px;
  margin: auto;
  padding-left: 25px;
  padding-right: 25px;
  p {
    color: #555;
  }
}
.navbar {
  display: flex;

  align-items: center;
  padding: 5px;

  nav {
    margin-left: auto;

    ul {
      display: inline-block;
      list-style-type: none;

      li {
        display: inline-block;
        margin-right: 20px;
        a {
          text-decoration: none;
          color: #555;
        }
      }
    }
  }
  .logo-cart {
    position: relative;
    img {
      height: 30px;
      width: 30px;
      margin-right: 4px;
    }
    .wrap-icon {
      top: 0;
      right: 0;
      position: absolute;
      width: 19px;
      height: 19px;
      background-color: #7E68E8;
      color: white;
      border-radius: 50px;
      font-size: 0.8rem;
      display: flex;
      font-weight: 700;
      align-items: center;
      justify-content: center;
    }
  }
}
.row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;

  .col-2 {
    flex-basis: 50%;
    min-width: 300px;
    img {
      max-width: 100%;
    }
    h1 {
      font-size: 50px;
      line-height: 60px;
      margin: 25px 0;
    }
    .offer-img {
      padding: 50px;
    }
    small {
      color: #555;
    }
  }
}
.header {
  background: radial-gradient(#fff, #ffffff);
}
.btn {

  display: inline-block;
  background: #019FFF;
  color: #fff !important;
  padding: 8px 30px;
  margin: 30px 0;
  border-radius: 30px;
  cursor: pointer;
  transition: background 0.5s;
  border: none;
}
.btn:hover {
  background: #fff;
  color: #000 !important;
  border: 1px solid #019FFF;
  color:#019FFF
}

.categories {
  margin: 70px 0;
}

.col-3 {
  flex-basis: 25%;
  min-width: 250px;
  margin-bottom: 30px;
  .img-auth {
    object-fit: cover;
    max-width: 50px;
    max-height: 50px;
  }
  img {
    width: 100%;
  }
  p {
    font-size: 12px;
    margin: 12px 0;
  }
}
.small-container {
  max-width: 1080px;
  margin: auto;
  padding-left: 25px;
  padding-right: 25px;
}

.col-4 {
  position: relative;
  flex-basis: 25%;
  padding: 10px;
  min-width: 200px;
  margin-bottom: 50px;
  transition: transform 0.5s;
  img {
    width: 100%;
  }
  p {
    font-size: 14px;
  }
}
.title {
  text-align: center;
  margin: 0 auto 80px;
  position: relative;
  line-height: 60px;
  color: #555;
}
.title::after {
  content: "";
  background: #009FFF;
  width: 80px;
  height: 5px;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);

  h4 {
    color: #555;
    font-weight: normal;
  }
}

.rating {
  .fa {
    color: #FEBC3B;
  }
}

.col-4:hover {
  transform: translateY(-5px);
}

.offer {
  background: radial-gradient(#fff, #ffffff);
  margin-top: 80px;
  padding: 30px;
}

.testimonial {
  padding-top: 100px;

  .col-3 {
    text-align: center;
    padding: 40px 20px;
    box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: transform 0.5s;

    img {
      width: 50px;
      margin-top: 20px;
      border-radius: 50%;
    }
    h3 {
      font-weight: 600;
      color: #555;
      font-size: 16px;
    }
  }
  .col-3:hover {
    transform: translateY(-10px);
  }
}

.fa.fa-quote-left {
  font-size: 34px;
  color: #ff523b;
}

.brands {
  margin: 100px auto;

  .col-5 {
    width: 160px;
    img {
      width: 100%;
      cursor: pointer;
      filter: grayscale(100%);
    }
    img:hover {
      filter: grayscale(0);
    }
  }
}

.footer {
  margin-top: 60px;

  background: #000;
  color: #8a8a8a;
  font-size: 14px;
  padding: 60px 0 20px;

  p {
    color: #8a8a8a;
  }
  h3 {
    color: #fff;
    margin-bottom: 20px;
  }
  .footer-col-1,
  .footer-col-2,
  .footer-col-3,
  .footer-col-4 {
    min-width: 250px;
    margin-bottom: 20px;
  }
  .footer-col-1 {
    flex-basis: 30%;
  }
  .footer-col-2 {
    flex: 1;
    text-align: center;

    img {
      width: 180px;
      margin-bottom: 20px;
    }
  }
  .footer-col-3,
  .footer-col-4 {
    flex-basis: 12%;
    text-align: center;
  }
  ul {
    list-style-type: none;
  }

  .app-logo {
    margin-top: 20px;
    img {
      width: 140px;
    }
  }
  hr {
    border: none;
    background: #b5b5b5;
    height: 1px;
    margin: 20px 0;
  }
  .copyright {
    text-align: center;
  }
}

.menu-icon {
  width: 28px;
  margin-left: 20px;
  display: none;
}

/* All product page*/
.row-2 {
  justify-content: space-between;
  margin: 100px auto 50px;
}
.page-btn {
  margin: 0 auto 80px;
  span {
    display: inline-block;
    border: 1px solid #ff523b;
    margin-left: 10px;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    cursor: pointer;
  }
  span:hover {
    background: #ff523b;
    color: #fff;
  }
}

/* Product Item */

.product-item {
  margin-top: 80px;

  .col-2 {
    padding: 20px;
    img {
      padding: 0;
    }
  }
  h4 {
    margin: 20px 0;
    font-size: 22px;
    font-weight: bold;
  }
  select {
    display: block;
    padding: 10px;
    margin-top: 20px;
  }
  input {
    width: 50px;
    height: 40px;
    padding-left: 10px;
    font-size: 20px;
    margin-right: 10px;
    border: 1px solid #ff523b;
  }
  input:focus {
    outline: none;
  }
  .fa {
    color: #ff523b;
    margin-left: 10px;
  }
}

.small-img-row {
  display: flex;
  justify-content: space-between;

  .small-img-rol {
    flex-basis: 24%;
    cursor: pointer;
  }
}

/* Cart item*/
.cart-page {
  margin: 80px auto;
  table {
    width: 100%;
    border-collapse: collapse;
  }
  th {
    text-align: left;
    padding: 5px;
    color: #fff;
    background: #BCBDBA;
    font-weight: normal;
  }
  td {
    padding: 10px 5px;
    input {
      width: 50px;
      height: 30px;
      padding: 5px;
    }
    .btn-remove {
      color: #ff523b;
      font-size: 12px;
      background: none;
      border: none;
      font-weight: 700;
      cursor: pointer;
    }
    img {
      width: 100px;
      height: 100px;
      margin-right: 10px;
      object-fit: cover;
    }
  }
  .cart-info {
    display: flex;
    flex-wrap: wrap;
  }
}
.btn-payment {
  display: flex;

  div {
    margin-left: auto;
    background: #1890ff;
    color: #fff;
  }
}

.total-price {
  display: flex;
  justify-content: flex-end;
  & table {
    border-top: 3px solid #26A0FC;
    width: 100%;
    max-width: 400px;
  }
}
td:last-child {
  text-align: right;
}
th:last-child {
  text-align: right;
}

// Media query for menu
$medium: 800px;
@media (max-width: $medium) {
  .navbar {
    nav {
      ul {
        position: absolute;
        top: 70px;
        left: 0;
        background: #fff;
        width: 100%;
        overflow: hidden;
        transition: max-height 0.5s;
        li {
          display: block;
          margin-right: 50px;
          margin-top: 10px;
          margin-bottom: 10px;
          & a {
            color: #333;
          }
        }
      }
    }
    .menu-icon {
      display: block;
      cursor: pointer;
    }
  }
}

$small: 400px;
@media (max-width: $small) {
  #MenuItems {
    display: none !important;
  }
}

$normal: 600px;
/* Media query for 600 screen size*/
@media (max-width: $normal) {
  .row {
    text-align: center;
  }
  .col-2,
  .col-3,
  .col-4 {
    flex-basis: 100%;
  }
  .product-item {
    .row {
      text-align: left;
    }
    .col-2 {
      padding: 20px 0;
    }
    h1 {
      font-size: 26px;
      line-height: 26px;
    }
  }
  .cart-info p {
    display: none;
  }
}

$blue-gradient: linear-gradient(45deg, #0136af, #22abfa);
$red-gradient: linear-gradient(45deg, #d62926, #ee625f);
$green-gradient: linear-gradient(45deg, #11998e, #1ce669);
$orange-gradient: linear-gradient(45deg, #fc4a1a, #f7b733);
$black-gradient: linear-gradient(45deg, #111, #666);

$gradientBgs: (
  "blue": $blue-gradient,
  "red": $red-gradient,
  "green": $green-gradient,
  "orange": $orange-gradient,
  "black": $black-gradient,
);

$box-shadow1: 15px 0 35px rgba(0, 0, 0, 0.1), 0 -15px 35px rgba(0, 0, 0, 0.1),
  0 15px 35px rgba(0, 0, 0, 0.1);

$box-shadow2: -15px 0 35px rgba(0, 0, 0, 0.1), 0 -15px 35px rgba(0, 0, 0, 0.1),
  0 15px 35px rgba(0, 0, 0, 0.1);

$box-shadow3: 0 0 10px 0.5px rgba(0, 0, 0, 0.2);

// Mixins

@mixin flex-centered($a: 1, $j: 1) {
  display: flex;
  @if ($a==1) {
    align-items: center;
  }
  @if ($j==1) {
    justify-content: center;
  }
}

// Working as an alias for flex-centered
@mixin fc($a: 1, $b: 1) {
  @include flex-centered($a, $b);
}

.title-color {
  color: #3a3a3a;
  font-weight: 600;
  font-size: 1.2rem;
  text-transform: uppercase;
}

.color-container {
  padding: 10px 0;
}

.colors {
  @include fc(1, 0);
  padding: 8px 0;
}

.color {
  cursor: pointer;

  border-radius: 50%;
  height: 25px;
  margin: 0 10px;
  transition: 0.5s;
  width: 25px;
}

.color[color="blue"] {
  background-color: #2175f5;
  border-color: #2175f5;
}

.color[color="red"] {
  background-color: #f84848;
  border-color: #f84848;
}

.color[color="green"] {
  background-color: #29b864;
  border-color: #29b864;
}

.color[color="orange"] {
  background-color: #ff5521;
  border-color: #ff5521;
}

.color[color="black"] {
  background-color: #444;
  border-color: #444;
}

.color.active {
  border-color: #fff;
  box-shadow: $box-shadow3;
  transform: scale(1.1);
}
